import React, { useState } from 'react';
import styled from 'styled-components';
import { StarActive, Star } from './elements';

const RatingField = props => {
  const { rating, change } = props;
  return (
    <Wrapper tabIndex={0}>
      <CaptionRating>
        <span></span>
        <span>
          <RatingNum> {rating}</RatingNum> / 10
        </span>
      </CaptionRating>
      <StarsContainer>
        {new Array(10).fill(null).map((item, i) => (
          <EmptyStar key={i} onClick={() => change(i + 1)}>
            {i + 1 <= rating ? <StarActive /> : <Star />}
          </EmptyStar>
        ))}
      </StarsContainer>
    </Wrapper>
  );
};

export default RatingField;

const Wrapper = styled.div`
  user-select: none;
  &:hover {
    background: hsl(35deg 40% 95%);
  }
  &:focus {
    background: hsl(35deg 40% 95%);
  }
  &:active {
    background: hsl(35deg 40% 85%);
  }
`;

const StarsContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  user-select: none;
`;

const EmptyStar = styled.div`
  font-size: 2em;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
`;

const CaptionRating = styled.div`
  display: flex;
  justify-content: space-between;
`;
const RatingNum = styled.span`
  font-size: 1.3em;
`;
